import styles from "./Games.module.css"

export default function Games({SelectedTeam , info}){
  if(!info){
    return ""
  }
  console.log(info)
  return (
    <div className={styles.container}>
      {
        info.map((item , index )=> {
          
          if(SelectedTeam===item.awayTeam.name || SelectedTeam===item.homeTeam.name) {
          if(item.status === "FINISHED"){
            return <div key={index} className={styles.team}><span className={styles.first}>{item.homeTeam.name} </span><span className={styles.score}>{item.score.fullTime.homeTeam} : {item.score.fullTime.awayTeam}</span> <span className={styles.second}> {item.awayTeam.name}</span></div>
          }else if(item.status === "POSTPONED") {
            return <div key={index}className={styles.team}><span className={styles.first}>{item.homeTeam.name}</span> <span className={styles.score}>XXX</span>  <span className={styles.second}>{item.awayTeam.name}</span></div>
          }
          return <div key={index} className={styles.team} ><span className={styles.first}>{item.homeTeam.name}</span> <span className={`${styles.score} ${styles.scoreLeft}`}>:</span><span className={styles.second}>{item.awayTeam.name}</span></div>
          
          }
        })
      }
    </div>
  )
}